.About_title__2wcYM {
    font-family: Akira;
    color: white;
    font-size: 4vw;
    font-weight: 400;
    /* line-height: 150px; */
    height: 100%;
    background: linear-gradient(
      90.21deg,
      #6fffff 0.13%,
      #ff8eff 48.16%,
      #b54eff 99.82%
    );

    background-size: 200% 200%;
	  -webkit-animation: About_gradient__1O0L6 3s ease infinite;
	          animation: About_gradient__1O0L6 3s ease infinite;

    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  
    margin-top: 4%;
    height: auto;
    margin-bottom: 4%;
}

@-webkit-keyframes About_gradient__1O0L6 {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

@keyframes About_gradient__1O0L6 {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

