.About_glow__pW-wx {
  width: 100%;
  height: 100%;
  -webkit-animation: About_scale__Kv1QS 5s infinite;
          animation: About_scale__Kv1QS 5s infinite;
}

.About_card__34P87 {
  width: 42%;
  /* height: 300px; */
  z-index: 5;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  -webkit-animation: About_floating__3bEXV 3.5s infinite ease-in-out;
          animation: About_floating__3bEXV 3.5s infinite ease-in-out;
  /* cardscale 7s infinite ease-in-out */
}

@-webkit-keyframes About_floating__3bEXV {
    0% { -webkit-transform: translate(0,  0px) scale(1); transform: translate(0,  0px) scale(1);
    }
    50%  { -webkit-transform: translate(0, 15px) scale(0.97); transform: translate(0, 15px) scale(0.97); }
    100%   { -webkit-transform: translate(0, -0px) scale(1); transform: translate(0, -0px) scale(1);}   
}

@keyframes About_floating__3bEXV {
    0% { -webkit-transform: translate(0,  0px) scale(1); transform: translate(0,  0px) scale(1);
    }
    50%  { -webkit-transform: translate(0, 15px) scale(0.97); transform: translate(0, 15px) scale(0.97); }
    100%   { -webkit-transform: translate(0, -0px) scale(1); transform: translate(0, -0px) scale(1);}   
}

@-webkit-keyframes About_scale__Kv1QS {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    50% {
      -webkit-transform: scale(0.8);
              transform: scale(0.8);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
}

@keyframes About_scale__Kv1QS {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    50% {
      -webkit-transform: scale(0.8);
              transform: scale(0.8);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
}

@-webkit-keyframes About_cardscale__BLB9A {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    50% {
      -webkit-transform: scale(0.95);
